<template>
  <div class="common-task-container">
    <div class="common-task-line text_primary">{{item.description}}</div>
    <div class="common-task-line">
      <span>奖励：<span class="text_warning">{{item.reward}}</span></span>
      <mt-button class="common-task-button mint-button--success" :disabled="taskStatus > 0" plain @click.native="handleClick">{{taskStatusStr}}</mt-button>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import { Component, Prop } from 'vue-property-decorator'
  import { Getter, Action } from 'vuex-class'
  import { momentFormatter } from '../../utils/dateFormatter'
  import { MessageBox, Toast } from 'mint-ui'

  @Component
  export default class CommonTask extends Vue {
    @Prop(Object) item;

    @Getter('getTasks') tasks;
    @Action('updateTaskTimestamp') updateTaskTimestamp;
    @Action('updateGold') updateGold;

    // 大于0表示还差多久，小于0表示冷却完成
    get taskStatus () {
      let task = this.tasks[this.item.name];
      if (!task) {
        return 0
      }
      let now = Date.now();
      let finishTime = Number(task.timestamp);
      return finishTime - now
    }

    get taskStatusStr () {
      if (this.taskStatus > 0) {
        return `冷却中(${momentFormatter(this.taskStatus)})`
      }
      return '完成'
    }

    handleClick () {
      MessageBox({
        title: '提示',
        message: `确定提交任务【${this.item.description}】`,
        showCancelButton: true
      }).then(action => {
        if (action === 'confirm') {
          this.updateGold(this.item.gold);
          let now = Date.now();
          let next = now + this.item.interval;
          this.updateTaskTimestamp({
            taskName: this.item.name,
            timestamp: next
          });
          Toast('任务完成！奖励已发放~');
        } else {
          this.$router.push('/my')
        }
      });
    }

  }
</script>

<style scoped>
  .common-task-container {
    margin: 5px;
    padding: 5px 0;
    border-bottom: 1px solid #DCDFE6;
  }
  .common-task-button {
    font-size: 14px;
    height: auto;
    padding: 2px 10px;
    float: right;
  }
  .common-task-line {
    padding: 5px 0;
  }
</style>


