import { types } from './mutations';
import { saveUserInfo } from '../../api/userInfo'

const actions = {
  // 修改金币
  updateGold (context, payload) {
    let userInfo = context.state.userInfo;
    if (!userInfo.gold) {
      userInfo.gold = 0;
    }
    userInfo.gold += payload;
    context.commit(types.UPDATE_USER_INFO, userInfo);
    saveUserInfo(userInfo)
  }
};

export default actions;



