import Vue from 'vue'
import Router from 'vue-router'
import Task from '../views/task/index.vue'
import My from '../views/my/index.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '',
      redirect: '/task'
    },
    {
      path: '/task',
      name: 'task',
      component: Task
    },
    {
      path: '/my',
      name: 'my',
      component: My
    }
  ]
})



