export function getUserInfo () {
  let userInfo = window.localStorage.getItem('userInfo')
  if (!userInfo) {
    return {
      name: '公子世无双',
      zdl: 0,
      gold: 0
    }
  }
  return JSON.parse(userInfo)
}

export function saveUserInfo (userInfo) {
  window.localStorage.setItem('userInfo', JSON.stringify(userInfo))
}



