export function momentFormatter (moment) {
  if (moment / 3600000 > 1) {
    return `${Math.floor(moment / 3600000)}h`
  }
  if (moment / 60000 > 1) {
    return `${Math.floor(moment / 60000)}min`
  }
  if (moment / 1000 > 1) {
    return `${Math.floor(moment / 1000)}s`
  }
  return `${moment}ms`
}



