export function getTasks () {
  let tasks = window.localStorage.getItem('tasks')
  if (!tasks) {
    return {}
  }
  return JSON.parse(tasks)
}

export function saveTasks (tasks) {
  window.localStorage.setItem('tasks', JSON.stringify(tasks))
}


