import Vue from 'vue';
import { types } from './mutations';
import { saveTasks } from '../../api/task'

const actions = {
  updateTasks (context, payload) {
    context.commit(types.UPDATE_TASKS, payload);
  },
  // 修改task时间戳
  updateTaskTimestamp (context, payload) {
    let tasks = context.state.tasks;
    let { taskName, timestamp } = payload;
    let task = tasks[taskName];
    if (!task) {
      task = {}
      Vue.set(tasks, taskName, task)
    }
    task.timestamp = timestamp;
    context.commit(types.UPDATE_TASKS, tasks);
    saveTasks(tasks)
  }
};

export default actions;



