<template>
  <div>
    <div class="my-block">
      <img class="info-icon-img" width="80" height="80" src="TX/defaultTX.jpg">
      <div class="info-by-icon">
        <div class="icon-info-item__large icon-info-item__name">{{userInfo.name}}</div>
        <div class="icon-info-item__large">战斗力 <span class="icon-info-item__zdl">{{userInfo.zdl}}</span></div>
        <div class="icon-info-item__small">灵石 <span class="icon-info-item__ls">{{userInfo.gold}}</span></div>
      </div>
    </div>
    <div class="my-block">
      更多功能~
    </div>
    <div class="my-block">
      敬请期待~
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import {Component} from 'vue-property-decorator'
  import { Getter } from 'vuex-class'

  @Component
  export default class My extends Vue {
    @Getter('getUserInfo') userInfo
  }
</script>

<style scoped>
  .my-block {
    padding: 10px;
    margin: 10px 0;
    background-color: #FFF;
    border-radius: 5px;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  }

  .info-icon-img {
    border: 2px solid #DCDFE6;
    border-radius: 10px;
  }

  .info-by-icon {
    display: inline-block;
    height: 80px;
    vertical-align: top;
    margin-left: 20px;
  }
  .icon-info-item__large {
    height: 30px;
  }

  .icon-info-item__small {
    font-size: 14px;
    height: 20px;
  }

  .icon-info-item__zdl {
    font-size: larger;
    color: #F56C6C;
    text-shadow: -1px 1px 5px #990066;
    padding-left: 10px;
  }

  .icon-info-item__ls {
    padding-left: 10px;
    color: #409EFF;
  }
  .icon-info-item__name {
    color: #E6A23C;
    font-weight: bolder;
  }

</style>


