<template>
  <div>
    <component v-for="(item, index) in tasks" :key="index" :is="`${item.type}Task`" :item="item"></component>
  </div>
</template>

<script>
  import Vue from 'vue'
  import {Component} from 'vue-property-decorator'
  import { Getter, Action } from 'vuex-class'
  import CommonTask from './commonTask.vue'
  import taskList from './taskList'
  import { getTasks } from '../../api/task'

  @Component({
    components: {
      CommonTask
    }
  })
  export default class index extends Vue {
    @Getter('getUserInfo') userInfo;
    @Getter('getTasks') tasksBuffer;
    @Action('updateTasks') updateTasks

    get tasks () {
      let now = Date.now()
      return taskList.sort((a, b) => {
        let taskBuffer_a = this.tasksBuffer[a.name]
        let taskBuffer_b = this.tasksBuffer[b.name]
        let isAble_a = true
        let isAble_b = true
        if (taskBuffer_a && taskBuffer_a.timestamp > now) {
          isAble_a = false
        }
        if (taskBuffer_b && taskBuffer_b.timestamp > now) {
          isAble_b = false
        }
        if (isAble_a) {
          return -1
        }
        if (!isAble_a && isAble_b) {
          return 1
        }
        return 0
      })
    }

    created () {
      this.updateTasks(getTasks())
    }
  }
</script>

<style scoped></style>


