<template>
  <mt-tabbar fixed v-model="selected">
    <mt-tab-item id="task">
      <i slot="icon" class="iconfont icon-calendar"></i>
      任务
    </mt-tab-item>
    <mt-tab-item id="signon">
      <i slot="icon" class="iconfont icon-post"></i>
      签到
    </mt-tab-item>
    <mt-tab-item id="otherApps">
      <i slot="icon" class="iconfont icon-apps"></i>
      其他
    </mt-tab-item>
    <mt-tab-item id="my">
      <i slot="icon" class="iconfont icon-people"></i>
      我的
    </mt-tab-item>
  </mt-tabbar>
</template>

<script>
  import Vue from 'vue';
  import { Component, Watch } from 'vue-property-decorator';

  @Component
  export default class FixedFooter extends Vue {
    selected = '/task';

    @Watch('selected')
    onSelected (val) {
      this.$router.push(`/${val}`)
    }

    created () {
      let path = this.$route.path.substring(1)
      this.selected = path
    }
  }
</script>

<style scoped>
</style>


